import { Card, CardHeader, CardContent } from "../../../../../components/Card"
import { Badge } from "../../../../../components/Badge"
import AddNewButton from "@/components/add-button"
import { useRouter } from "next/router";

export default function RecruiterDashboard() {
  const router = useRouter();
  const id=router.query.jobid
  return (
    <div className="max-w-[1200px] mx-auto p-4 md:p-6 space-y-6">
      {/* Profile Info Section */}
      <section>
        <h2 className="text-xl font-semibold mb-4">Profile Info</h2>
        <Card>
          <CardContent>
            <h3 className="text-xl font-semibold mb-4">Nanthakumar Krishnamoorthy</h3>

            <div className="grid grid-cols-1 md:grid-cols-2 gap-4 mb-4">
              <div className="flex items-center gap-6">
                <span className="text-gray-500">Web Developer</span>
                <span>Dell Technologies</span>
              </div>
            </div>

            <div className="grid grid-cols-2 md:grid-cols-4 gap-4 mb-4 text-sm">
              <span className="text-gray-600">Remote</span>
              <span className="text-gray-600">Male</span>
              <span className="text-gray-600">Immediate Joiner</span>
              <span className="text-gray-600">Unmarried</span>
            </div>

            <div className="grid grid-cols-2 md:grid-cols-4 gap-4 text-sm">
              <span className="text-gray-600">Chennai</span>
              <span className="text-gray-600">B.sc Physics</span>
              <span className="text-gray-600">Fresher</span>
              <span className="text-gray-600">2,50,000 - 3,00,000</span>
            </div>
          </CardContent>
        </Card>
      </section>

      {/* Job Info Section */}
      <section>
        <h2 className="text-xl font-semibold mb-4">Job Info</h2>
        <Card>
          <CardHeader>
            <div>
              <h3 className="text-xl font-semibold">Java Developer</h3>
              <p className="text-blue-500 text-sm">SeerTech Systems</p>
            </div>
            <button className="bg-blue-500 hover:bg-blue-600 text-white py-2 px-4 rounded">Update</button>
          </CardHeader>
          <CardContent className="bg-gray-50">
            <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
              <div className="space-y-4">
                <div>
                  <h4 className="font-medium text-sm mb-1">Experience</h4>
                  <p className="text-gray-600">0-3 Yrs</p>
                </div>
                <div>
                  <h4 className="font-medium text-sm mb-1">Location</h4>
                  <p className="text-gray-600">Gurugram</p>
                </div>
              </div>
              <div className="space-y-4">
                <div>
                  <h4 className="font-medium text-sm mb-1">Salary</h4>
                  <p className="text-gray-600">1-3.75 Lacs PA</p>
                </div>
                <div>
                  <h4 className="font-medium text-sm mb-1">Job Code</h4>
                  <p className="text-gray-600">Job Code: XYZ</p>
                </div>
              </div>
              <div className="space-y-4">
                <div>
                  <h4 className="font-medium text-sm mb-1">Skills Required</h4>
                  <p className="text-gray-600">Java, SQL, Spring Boot</p>
                </div>
                <div>
                  <h4 className="font-medium text-sm mb-1">Inserted By</h4>
                  <p className="text-gray-600">Pranoti</p>
                </div>
              </div>
            </div>
          </CardContent>
        </Card>
      </section>

      {/* Interviews Section */}
      <section>
        <div className="flex justify-between items-center mb-4">
          <h2 className="text-xl font-semibold">Interviews</h2>
          <div className="flex items-center gap-2">
            <span className="text-gray-600">Over all status</span>
            <Badge variant="success">Selected</Badge>
          </div>
        </div>

        <div className="space-y-4">
          {/* Round 1 */}
          <Card>
            <CardHeader>
              <h3 className="font-semibold text-lg">Round 1</h3>
              <button className="bg-blue-500 hover:bg-blue-600 text-white py-2 px-4 rounded">Update</button>
            </CardHeader>
            <CardContent>
              <div className="flex justify-between items-start mb-6">
                <div>
                  <h4 className="font-medium text-base">Technical Call Round</h4>
                  <p className="text-sm text-gray-500">23/11/24</p>
                </div>
                <Badge variant="secondary">Via Call</Badge>
                <Badge variant="success">Passed</Badge>
              </div>

              <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
                <div>
                  <p className="text-sm text-gray-500 mb-1">Interviewer Name</p>
                  <p className="text-gray-700">Pranoti</p>
                </div>
                <div>
                  <p className="text-sm text-gray-500 mb-1">Technology Interviewed</p>
                  <p className="text-gray-700">Java,Python,React JS</p>
                </div>
                <div className="flex gap-8">
                  <div>
                    <p className="text-sm text-gray-500 mb-1">Tech Rating</p>
                    <p className="text-gray-700">8/10</p>
                  </div>
                  <div>
                    <p className="text-sm text-gray-500 mb-1">Soft skill Rating</p>
                    <p className="text-gray-700">9/10</p>
                  </div>
                </div>
              </div>

              <div className="mt-4">
                <p className="text-sm text-gray-500 mb-1">Remarks</p>
                <p className="text-gray-700">Excellent Technical Knowledge</p>
              </div>
            </CardContent>
          </Card>

          {/* Round 2 and 3 would be similar to Round 1, so I'm omitting them for brevity */}

          <div className="flex justify-center mt-8">
            <AddNewButton title="Add New Round" url=""></AddNewButton>
          </div>
        </div>
      </section>
    </div>
  )
}

